import React from 'react';
import ContnentBar from './ContnentBar';
import { Button } from 'antd-mobile'

const Content = (props) => {

    return (
        <div>
            
            <p style={{
                        backgroundColor:'rgba(0,0,0,0.4)',
                        color:'white',
                        width:'40px',
                        height:'40px',
                        fontSize:'35px',
                        lineHeight:'40px',
                        textAlign:'center',
                        borderRadius:'30px',
                        position:'absolute',
                        top:'15px',
                        left:'15px'
                    }}
                    onClick={()=>props.history.goBack()}
                    >
                        &lt;
                        </p>
            <img src='https://img.alicdn.com/imgextra/i4/929420007/TB2cM.KdY1YBuNjSszeXXablFXa_!!929420007.jpg_640x640q80_.webp'
                style={{ 
                    width: '100%',
                 }}
            />
            <div style={{width:'100%'}}>
                <div >
                    <ContnentBar />
                </div>
                <div style={{ width: '60%', position: 'fixed', bottom: 0, right: '-10px',backgroundColor:'white'}}
                    className='button' >
                    <Button style={{
                        display: 'inline-block',
                        width: '100px',
                        backgroundColor: '#FF6347',
                        color: 'white',
                        borderTopLeftRadius: '30px',
                        borderBottomLeftRadius: '30px',
                        fontSize:'15px'
                    }}>
                        加入购物车
                    </Button>
                    <Button style={{
                        display: 'inline-block',
                        width: '100px',
                        backgroundColor: '#FF4500',
                        color: 'white',
                        borderTopRightRadius: '30px',
                        borderBottomRightRadius: '30px',
                        fontSize:'15px'
                    }}>
                        立即购买
                    </Button>
                </div>
            </div>

        </div>
    )
}

export default Content