import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';



class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={{
                position: 'fixed',
                width: '100%',
                bottom: 0
            }}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-home'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-home'></i>
                        }
                        selected={pathname === '/'}
                        onPress={() => {
                            this.props.history.push('/');
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>

                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-gouwuche'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-gouwuche'></i>
                        }
                        title="购物车"
                        key="cart"
                        selected={pathname === '/Cart'}
                        onPress={() => {
                            this.props.history.push('/Cart')
                        }}
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className=' iconfont icon-liebiao'></i>
                        }
                        selectedIcon={
                            <i className=' iconfont icon-liebiao' ></i>
                        }
                        title="订单列表"
                        key="Friend"
                        selected={pathname === '/dingdan'}
                        onPress={() => {
                            this.props.history.push('/dingdan');
                        }}
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-04'></i>}
                        selectedIcon={
                            <i className=' iconfont icon-04'></i>
                        }
                        selectedIcon={<i className='iconfont icon-04-copy'></i>}
                        title="我的淘宝"
                        key="my"
                        selected={pathname === '/myTaobao'}
                        onPress={() => {
                            this.props.history.push('/myTaobao');
                        }}
                    >
                    </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);