import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';


class ContentBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={{
                position: 'fixed',
                width: '45%',
                bottom: 0
            }}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="home"
                        icon={
                            <i className='iconfont icon-dianpu'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dianpu-copy'></i>
                        }
                        selected={pathname === '/'}
                        data-seed="logId"
                    >
                    </TabBar.Item>

                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-taobaokefu-01'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-taobaokefu-01'></i>
                        }
                        title="客服"
                        key="cart"
                        selected={pathname === '/cart'}
                        data-seed="logId1"
                    ></TabBar.Item>

                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang1-copy'></i>
                        }
                        title="收藏"
                        key="cart"
                        selected={pathname === '/'}
                        data-seed="logId1"
                    ></TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(ContentBar);