import React, {useState,useEffect} from 'react';
// usestate函数组件实现状态的处理
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch, withRouter } from 'react-router-dom'

import Home from './taobao/Home';
import Cart from './taobao/Cart';
import Mytab from "./taobao/Mytab";
import Dingdan from './taobao/dingdan'
import MyTaobao from './taobao/myTaobao'
import SearchBarExample from './taobao/searchBar'
import Content from './taobao/Content';


//---------------------------------------------------------------------------------
//任务三

const App = () =>{
    return <Router>
        <Switch>
            <Route exact path="/" component={Home} />
            {/* path的地址一定要和最初打开页面的地址一样才能匹配上 */}
            <Route path="/Cart" component={Cart}  />
            <Route path="/dingdan" component={Dingdan}  />
            <Route path="/myTaobao" component={MyTaobao}  />
            <Route path='/searchBar' component={SearchBarExample} />
            <Route path='/Content' component={Content} />
            {/* 重定向,都没配上，跳转到首页 */}
            <Route component={Home} />
        </Switch>
    </Router>
}


export default App 
